@media (max-device-width: 500px) {
  .App {
    display: flex;
    flex-direction: column;
    background: linear-gradient(#10AEEF, #0F0F0F);
    font-family: "Venus";
    color: white;
  }

  h6 {
    font-size: 17px;
    text-align: justify;
    margin-left: 4%;
    margin-right: 4%;
  }

  h2 {
    font-size: 20px;
  }

  h1 {
    font-size: 24px;
  }

  h3 {
    display: flex;
    justify-content: flex-start;
  }
}

@media (min-device-width: 500px) {
  .App {
    display: flex;
    flex-direction: column;
    background: linear-gradient(#10AEEF, #0F0F0F);
    font-family: "Venus";
    color: white;
  }

  h6 {
    font-size: 17px;
  }

  h2 {
    font-size: 20px;
  }

  h1 {
    font-size: 24px;
  }

  h3 {
    display: flex;
    justify-content: flex-start;
  }
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "Venus";
  src: url("../src/Venus-BF651e78bbb893a.otf");
}